/** San Francisco Font */
@font-face {
  font-family: "San Francisco";
  font-weight: 100;
  src: url("../statics/assets/fonts/sanfranciscodisplay-thin-webfont.woff") format('woff');
}
@font-face {
  font-family: "San Francisco";
  font-weight: 300;
  src: url("../statics/assets/fonts/sanfranciscodisplay-regular-webfont.woff") format('woff');
}
/** Medium */
@font-face {
  font-family: "San Francisco";
  font-weight: 500;
  src: url("../statics/assets/fonts/sanfranciscodisplay-medium-webfont.woff") format('woff');
}
/** Bold */
@font-face {
  font-family: "San Francisco";
  font-weight: 700;
  src: url("../statics/assets/fonts/sanfranciscodisplay-bold-webfont.woff") format('woff');
}



