@import '../../style/includes.css';

.assignReg {
  width: 90%;
  margin: 0 auto;
}
.assignReg h5 {
  position: relative;
}
.assignReg h3 {
  margin: 10px 0;
  text-align: center;
}
.assignReg label {
  display: block;
  text-align: left;
  width: auto;
}
.assignReg label.inlineB {
  display: inline-block;
}
.boxFt {
  margin: 20px 0;
  text-align: center;
}
.boxFt button {
  margin: 0 20px 0 0
}
.unassign {
  display: block;
  margin: 0 0 10px 0;
  border: 2px solid #ddd;
  border-radius: 12px;
  background: #fff;
  padding: 5px 10px;
}
.unassign ul {
  
}
.auto {
  font-size: 15px;
  display: inline-block;
}
.moveBit {
  display: inline-block;
  background: #5cd8f9;
  border-radius: 20px;
  line-height: 15px;
  padding: 5px 10px;
  color: #fff;
  font-size: 15px;
  margin: 0 5px;
  cursor: pointer;
}
.unassign .moveBit {
  background: #158fe2;
}
.modifiedMoveBit {
  composes: moveBit;
  background: #158fe2;
}
.races {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-flex-wrap: nowrap;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  margin: 0 0 10px 0;
}
.races>li {
  border: 2px solid #ddd;
  border-radius: 12px;
  background: #fff;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  margin: 0 10px 0 0;
  padding: 5px 10px;
  min-height: 120px;
}
.races>li:last-child {
  margin: 0;
}
.count {
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 5px;
  padding: 0 8px;
  border-radius: 8px;
  text-indent: 0;
  text-align: center;
  color: #dedede;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-family: 'Courier';
  background: #555;
  font-size: 12px;
  font-weight: 500;
  min-width: 30px;
  line-height: 22px;
}


